<template>
  <div id="songsDatail">
    <div class="detail">
      <!-- 封面 -->
      <img :src="image" alt class="cover" />
      <!-- 歌曲信息 -->
      <div class="singerInfo">
        <h1 class="musicName">{{musicName}}</h1>
        <span class="album">专辑：{{album}}</span>
        <div class="singer">
          歌手：
          <span v-for="item in singer" :key="item.name">{{item.name}}&nbsp;&nbsp;&nbsp;</span>
        </div>
        <div class="playMV" v-if="mvId">
          <img src="../assets/img/play.png" alt class="mv-btn" @click="playMV" />播放MV
        </div>
        <div class="playMV" v-else>
          <img src="../assets/img/noplay.png" alt class="mv-btn" @click="mvWarn" />播放MV
        </div>
      </div>
      <!-- 热门评论 -->
      <div class="comment">
        <div class="comment-list" v-for="item in comments" :key="item.name">
          <img :src="item.user.avatarUrl" alt class="avatar" />
          <span class="userName">{{item.user.nickname}}</span>
          <br />
          <p class="comment-content">{{item.content}}</p>
        </div>
      </div>
    </div>

    <!-- 播放条 -->
    <audio :src="musicUrl" controls preload loop class="audio_con" v-show="isCanPlay"></audio>
  </div>
</template>

<script>
import { getMusicDetail, getMusic, getComment } from "../network/axios";
export default {
  data() {
    return {
      id: null,
      image: null,
      musicUrl: null,
      musicName: "",
      singer: [],
      album: "",
      comments: [],
      mvId: null,
      isCanPlay: true,
    };
  },
  methods: {
    //普通方法
    playMV() {
      this.$router.push("/mv/" + this.mvId);
    },
    musicWarn() {
      this.$notify({
        title: "抱歉",
        message: "该歌曲暂时没有版权！",
        type: "warning",
      });
    },
    mvWarn() {
      this.$notify.info({
        title: "抱歉",
        message: "该歌曲没有MV",
      });
    },

    // 获取数据方法
    getData() {
      getMusicDetail(this.id).then((res) => {
        this.image = res.data.songs[0].al.picUrl;
        this.album = res.data.songs[0].al.name;
        this.singer = res.data.songs[0].ar;
        this.musicName = res.data.songs[0].name;
        this.mvId = res.data.songs[0].mv;
      });
      getMusic(this.id).then((res) => {
        this.musicUrl = res.data.data[0].url;
        if (this.musicUrl == null) {
          this.isCanPlay = false;
          this.musicWarn();
        }
      });
      getComment(this.id).then((res) => {
        this.comments = res.data.hotComments;
      });
    },
  },
  created() {
    this.id = this.$route.params.id;
    this.getData();
  },
  filters: {
    showDate: function (value) {
      let date = new Date(value * 1000);
      return formatDate(date, "yyyy-MM-dd");
    },
  },
};
</script>

<style lang="less">
#songsDatail {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  // background: url("../assets/img/bg.jpg");
  background-image: linear-gradient(to right, #085f94, #43677d);
  .detail {
    display: flex;
    width: 100%;
    height: 460px;
    .cover {
      margin: 10px 0 0 20px;
      width: 460px;
      border-radius: 10%;
      box-shadow: 9px 13px 8px rgba(63, 63, 63, 0.6);
    }
    .singerInfo {
      display: flex;
      flex-direction: column;
      width: 300px;
      height: 100%;
      color: white;
      .musicName {
        margin: 0;
        font-size: 18px;
        line-height: 250px;
        flex: 1;
        font-style: bold;
      }
      .album {
        line-height: 20px;
        flex: 1;
        font-style: italic;
      }
      .singer {
        line-height: 20px;
        flex: 1;
        font-style: italic;
      }
      .playMV {
        flex: 1;
      }
      .mv-btn {
        width: 50px;
        vertical-align: middle;
        margin-right: 5px;
        cursor: pointer;
      }
    }
    .comment {
      width: 420px;
      margin-right: 10px;
      color: white;
      text-align: left;
      height: 100%;
      overflow: auto;
      line-height: 20px;
      .comment-list {
        float: left;
      }
    }
    .comment::-webkit-scrollbar {
      display: none;
    }
    .userName {
      color: rgb(212, 145, 145);
    }
    .comment-content {
      margin: 0px 0 15px 50px;
      text-indent: 32px;
      color: rgb(141, 135, 135);
    }
    .avatar {
      width: 50px;
      border-radius: 30%;
      vertical-align: top;
    }
  }
  .audio_con {
    position: absolute;
    height: 60px;
    width: 100%;
    bottom: 10px;
    left: 0;
  }
}
</style>